$font-stack:     -apple-system, BlinkMacSystemFont, sans-serif;
$primary-color: rgb(1, 1, 1);
$secondary-color: rgb(1, 1, 1);
//$color:iron

:root {
  /* spacing values */
  --space-xxxs: 0.25em;
  --space-xxs:  0.375em;
  --space-xs:   0.5em;
  --space-sm:   0.75em;
  --space-md:   1.25em;
  --space-lg:   2em;
  --space-xl:   3.25em;
  --space-xxl:  5.25em;
  --space-xxxl: 8.5em;
}
//wtf??????
title{
  color: $primary-color
}


body {
  background-color: #F6F7F9;
  font: 100% $font-stack;
  color: $primary-color;
  line-height: 2.0
 
  
}

